.wrapper {
  display: flex;
  font-size: 90%;
}

.image-left {
  width: 35vw;
  height: 100vh;
  background-color: #f2d184;
}

.image-left span {
  position: relative;
  top: 5rem;
  left: 3rem;
  color: #846015;
  font-size: 2rem;
  font-weight: bold;
}

.image-left img {
  position: relative;
  top: 5rem;
  left: 3rem;
  width: 23rem;
}

.image-left p {
    position: relative;
    color: #846015;
    top: 20vh;
    left: 5rem
}

.image-left p a:link {
    text-decoration: none;
    color: #846015;
    text-decoration: underline;
}

.form-wrapper {
  width: 65vw;
  height: 100vh;

  display: block;
  text-align: center;
}

.signin {
  position: absolute;
  right: 30px;
  top: 20px;
}

form {
  width: 27rem;
  display: inline-block;
  margin: 3rem auto 0 auto;
  text-align: left;
}

.subdiv {
  display: flex;
  justify-content: space-between;
}

.input {
  display: flex;
  flex-direction: column;
  margin-top: 2rem;
  font-weight: bold;
}

input {
  border: none;
  border-bottom: 3px white solid;
  height: 1.5rem;
  padding: 0.5rem;
  padding-left: 1rem;
  border-radius: 0.5rem;
  margin-top: 0.5rem;
  background-color: #f3f3f3;
}

input:focus {
  outline: none;
  border-bottom: #f2d184 3px solid;
}

.name {
  width: 48%;
}

.terms {
  margin-top: 1.5rem;
  display: flex;
}

.container {
  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 12px;
  cursor: pointer;
}

.container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 20px;
  width: 20px;
  border: 2px solid #c7c5c5;
  border-radius: 0.2rem;
}

.container span {
  color: #777;
  font-size: 0.9rem;
}

.container:hover input ~ .checkmark {
  background-color: #ccc;
}

.container input:checked ~ .checkmark {
  background-color: #f2d184;
}

.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

.container input:checked ~ .checkmark:after {
  display: block;
}

.container .checkmark:after {
  left: 6px;
  top: 3px;
  width: 5px;
  height: 10px;
  border: solid white;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}

.submit button {
  border: none;
  border: 2px transparent solid;
  padding: 0.75rem;
  border-radius: 0.5rem;
  width: 12rem;
  font-weight: bold;
  color: #eee;
  background-color: #ea4b8b;
  margin-top: 1rem;
  transition: 0.3s;
}

.submit button:hover {
  color: #f00a66;
  background-color: white;
  border: 2px solid #f00a66;
}

.submit button:active {
  color: #f00a66;
  background-color: white;
  border: 2px solid #f00a66;
}

a:link {
  text-decoration: none;
}
a:visited {
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}
a:active {
  text-decoration: none;
}

.subbutton {
  font-size: 0.75rem;
  color: grey;
}

.subbutton p {
  margin: 0;
  margin-top: 1rem;
}
